// lib/api/video-api.ts
/**
 * Unified Video API shim
 *
 * - Re-exports the modular APIs so existing imports still work
 * - Provides a legacy `videoApi` object that proxies to movie/tv modules
 * - Default export is the legacy `videoApi` object (backwards compatible)
 */

/* Re-export core utilities, types, helpers */
export * from './video-common';

/* Re-export movie and tv specific modules */
export * from './video-movie';
export * from './video-tv';

/* Import the movie and tv modules to build a legacy shim */
import { movieVideoApi } from './video-movie';
import { tvVideoApi } from './video-tv';
import {
  getConfigurationStatus,
  validateConfiguration,
  logConfiguration,
  VIDEO_PROVIDERS,
  getEmbedUrlCandidates as commonGetEmbedUrlCandidates
} from './video-common';

/* Build the legacy videoApi object (keeps older code working) */
export const videoApi = {
  /* Movie methods */
  async getMovieSources(tmdbId: number) {
    return movieVideoApi.getMovieSources(tmdbId);
  },

  async getMovieWithEmbed(tmdbId: number) {
    return movieVideoApi.getMovieWithEmbed(tmdbId);
  },

  async getMovieSourcesWithTesting(tmdbId: number, testUrls = true) {
    return movieVideoApi.getMovieSourcesWithTesting(tmdbId, testUrls);
  },

  /* TV methods */
  async getTVSources(tmdbId: number, season?: number, episode?: number) {
    return tvVideoApi.getTVSources(tmdbId, season, episode);
  },

  async getTVShowWithEmbed(tmdbId: number, season?: number, episode?: number) {
    return tvVideoApi.getTVShowWithEmbed(tmdbId, season, episode);
  },

  async getTVSourcesWithTesting(tmdbId: number, season?: number, episode?: number, testUrls = true) {
    return tvVideoApi.getTVSourcesWithTesting(tmdbId, season, episode, testUrls);
  },

  /* Common utilities */
  hasConfiguredApis(): boolean {
    return VIDEO_PROVIDERS.length > 0;
  },

  getConfigStatus() {
    return getConfigurationStatus();
  },

  getEmbedUrlCandidates(mediaType: 'movie' | 'tv', tmdbId: number, season?: number, episode?: number) {
    if (mediaType === 'movie') {
      return movieVideoApi.getMovieEmbedCandidates(tmdbId);
    } else {
      return tvVideoApi.getTVEmbedCandidates(tmdbId, season, episode);
    }
  },

  logConfiguration() {
    logConfiguration();
  },

  /* Expose sub-APIs */
  movie: movieVideoApi,
  tv: tvVideoApi,
};

/* Default export maintains compatibility */
export default videoApi;
