// lib/api/video-common.ts
// Shared utilities, types and helpers for video APIs

export interface VideoSource {
  url: string;
  quality: string;
  type: "iframe";
  servers: string;
  tmdbId: number;
  mediaType: "movie" | "tv";
  season?: number;
  episode?: number;
  title?: string;
  episodeTitle?: string;
  responseTime?: number;
  working?: boolean;
  error?: string;
  provider?: string;
}

export interface VideoProvider {
  name: string;
  value: string;
  priority: number;
  mediaTypes: ("movie" | "tv")[];
  isTemplate: boolean;
  hostname: string;
}

export interface EmbedCheckResponse {
  success: boolean;
  workingUrl?: {
    provider: string;
    url: string;
    originalUrl: string;
    type: string;
  };
  totalTested: number;
  workingCount: number;
  allUrls: Array<{
    provider: string;
    url: string;
    originalUrl: string;
    type: string;
    working: boolean;
    responseTime?: number;
    error?: string;
  }>;
  metadata: {
    mediaType: string;
    tmdbId: number;
    season?: number;
    episode?: number;
    tested: boolean;
    timestamp: string;
  };
}

/** Read and normalize environment variables with validation */
export const ENV = {
  BASE: (process.env.NEXT_PUBLIC_VIDEO_API_BASE || "").trim(),
  E1: (process.env.NEXT_PUBLIC_VIDEO_EMBED_1 || "https://cinemaos.tech/player/${id}?autoplay=true").trim(),
  E2: (process.env.NEXT_PUBLIC_VIDEO_EMBED_2 || "https://cinemaos.tech/player/${id}/${season}/${episode}?autoplay=true&autonext=true").trim(),
  E3: (process.env.NEXT_PUBLIC_VIDEO_EMBED_3 || "").trim(),
  E4: (process.env.NEXT_PUBLIC_VIDEO_EMBED_4 || "").trim(),
  TMDB_API_KEY: (process.env.NEXT_PUBLIC_TMDB_API_KEY || "").trim(),
};

/** Global extra params appended to every embed URL */
export const EXTRA_PARAMS = (process.env.NEXT_PUBLIC_VIDEO_URL_PARAMS || "").trim();

/** Enhanced providers list with media type support */
export const VIDEO_PROVIDERS: VideoProvider[] = [
  { 
    name: "Primary", 
    value: ENV.E1, 
    priority: 1,
    mediaTypes: ["movie", "tv"],
    isTemplate: isTemplate(ENV.E1),
    hostname: getServerLabel(ENV.E1)
  },
  { 
    name: "Secondary", 
    value: ENV.E2, 
    priority: 2,
    mediaTypes: ["movie", "tv"],
    isTemplate: isTemplate(ENV.E2),
    hostname: getServerLabel(ENV.E2)
  },
  { 
    name: "Tertiary", 
    value: ENV.E3, 
    priority: 3,
    mediaTypes: ["movie", "tv"],
    isTemplate: isTemplate(ENV.E3),
    hostname: getServerLabel(ENV.E3)
  },
  { 
    name: "Backup", 
    value: ENV.E4, 
    priority: 4,
    mediaTypes: ["movie", "tv"],
    isTemplate: isTemplate(ENV.E4),
    hostname: getServerLabel(ENV.E4)
  },
  { 
    name: "Base", 
    value: ENV.BASE, 
    priority: 5,
    mediaTypes: ["movie", "tv"],
    isTemplate: isTemplate(ENV.BASE),
    hostname: getServerLabel(ENV.BASE)
  },
].filter(p => !!p.value);

/** Enhanced helper functions */
export function getServerLabel(url: string): string {
  if (!url) return 'Unknown Server';
  try {
    const hostname = new URL(url).hostname;
    return hostname.replace(/^www\./, '');
  } catch {
    return 'Unknown Server';
  }
}

export function buildEmbedPath(
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
): string {
  if (!tmdbId || tmdbId <= 0) {
    throw new Error(`Invalid TMDB ID: ${tmdbId}`);
  }

  if (mediaType === "movie") {
    return `/movie/${tmdbId}`;
  }
  
  if (mediaType === "tv") {
    if (season && episode) {
      return `/tv/${tmdbId}/${season}/${episode}`;
    }
    return `/tv/${tmdbId}`;
  }
  
  throw new Error(`Invalid media type: ${mediaType}`);
}

export function isTemplate(url: string): boolean {
  if (!url) return false;
  return /\$\{(id|type|season|episode)\}/.test(url);
}

export function normalizeBase(url: string): string {
  if (!url) return '';
  return url.endsWith("/") ? url.slice(0, -1) : url;
}

export function appendParams(url: string, params: string): string {
  if (!params || !url) return url;
  const separator = url.includes("?") ? "&" : "?";
  return `${url}${separator}${params}`;
}

export function buildRawUrl(
  providerValue: string,
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
): string {
  if (!providerValue) {
    throw new Error('Provider value is required');
  }

  if (!tmdbId || tmdbId <= 0) {
    throw new Error(`Invalid TMDB ID: ${tmdbId}`);
  }

  if (isTemplate(providerValue)) {
    // Template mode: replace placeholders
    return providerValue
      .replace(/\$\{id\}/g, String(tmdbId))
      .replace(/\$\{type\}/g, mediaType)
      .replace(/\$\{season\}/g, season ? String(season) : "1")
      .replace(/\$\{episode\}/g, episode ? String(episode) : "1");
  }
  
  // Base mode: append canonical path
  const base = normalizeBase(providerValue);
  const path = buildEmbedPath(mediaType, tmdbId, season, episode);
  return `${base}${path}`;
}

export function buildEmbedUrl(
  providerValue: string,
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
): string {
  try {
    const raw = buildRawUrl(providerValue, mediaType, tmdbId, season, episode);
    return appendParams(raw, EXTRA_PARAMS);
  } catch (error) {
    console.error('Failed to build embed URL:', error);
    throw error;
  }
}

/** Get providers filtered by media type */
export function getProvidersForMediaType(mediaType: "movie" | "tv"): VideoProvider[] {
  return VIDEO_PROVIDERS.filter(provider => 
    provider.mediaTypes.includes(mediaType)
  );
}

/** Get all embed URL candidates for a media type */
export function getEmbedUrlCandidates(
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
): string[] {
  const providers = getProvidersForMediaType(mediaType);
  const urls = providers.map(provider =>
    buildEmbedUrl(provider.value, mediaType, tmdbId, season, episode)
  );
  
  // Remove duplicates while preserving order
  return [...new Set(urls)];
}

/** Enhanced TMDB ID resolver with better error handling */
import axios from 'axios';
export async function resolveTmdbId(
  input: string | number,
  type: "movie" | "tv"
): Promise<number | null> {
  // Handle numeric input
  if (typeof input === "number" && Number.isFinite(input) && input > 0) {
    return input;
  }

  const str = String(input).trim();
  if (!str) return null;

  // Handle string numeric input
  const asNum = Number(str);
  if (Number.isFinite(asNum) && asNum > 0) {
    return asNum;
  }

  // Handle IMDb IDs
  if (/^tt\d+$/i.test(str)) {
    if (!ENV.TMDB_API_KEY) {
      console.error('TMDB API key is required to resolve IMDb IDs');
      return null;
    }
    
    try {
      const { data } = await axios.get(
        `https://api.themoviedb.org/3/find/${str}`,
        {
          params: { 
            api_key: ENV.TMDB_API_KEY, 
            external_source: "imdb_id" 
          },
          timeout: 5000
        }
      );
      
      const results = type === "movie" ? data.movie_results : data.tv_results;
      const tmdbId = results?.[0]?.id;
      
      if (!tmdbId) {
        console.warn(`No TMDB ${type} found for IMDb ID: ${str}`);
        return null;
      }
      
      return tmdbId;
    } catch (error) {
      console.error(`Failed to resolve IMDb ID ${str}:`, error);
      return null;
    }
  }

  console.warn(`Invalid input format for TMDB ID resolution: ${str}`);
  return null;
}

/** Configuration validation */
export function validateConfiguration(): {
  isValid: boolean;
  errors: string[];
  warnings: string[];
} {
  const errors: string[] = [];
  const warnings: string[] = [];

  // Check if we have any providers
  if (VIDEO_PROVIDERS.length === 0) {
    errors.push('No video providers configured');
  }

  // Check for TMDB API key
  if (!ENV.TMDB_API_KEY) {
    warnings.push('TMDB API key not configured - IMDb ID resolution will not work');
  }

  // Validate provider URLs
  VIDEO_PROVIDERS.forEach(provider => {
    if (!provider.value) {
      errors.push(`Provider "${provider.name}" has empty value`);
      return;
    }

    // Check if template providers have required placeholders
    if (isTemplate(provider.value)) {
      if (!provider.value.includes('${id}')) {
        errors.push(`Template provider "${provider.name}" missing \${id} placeholder`);
      }
    } else {
      // Check if base URL is valid
      try {
        new URL(provider.value);
      } catch {
        errors.push(`Provider "${provider.name}" has invalid base URL: ${provider.value}`);
      }
    }
  });

  return {
    isValid: errors.length === 0,
    errors,
    warnings
  };
}

/** Debug helper to get configuration status */
export function getConfigurationStatus() {
  const validation = validateConfiguration();
  
  return {
    providers: {
      total: VIDEO_PROVIDERS.length,
      movie: getProvidersForMediaType('movie').length,
      tv: getProvidersForMediaType('tv').length,
      details: VIDEO_PROVIDERS.map(p => ({
        name: p.name,
        hostname: p.hostname,
        isTemplate: p.isTemplate,
        mediaTypes: p.mediaTypes,
        priority: p.priority
      }))
    },
    environment: {
      tmdbApiKey: !!ENV.TMDB_API_KEY,
      extraParams: EXTRA_PARAMS || 'none'
    },
    validation
  };
}

/** Enhanced API call to check embed URLs (client-side convenience) */
export async function checkEmbedUrls(
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number,
  testUrls: boolean = true
): Promise<EmbedCheckResponse | null> {
  try {
    const response = await fetch('/api/check-embed', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        mediaType,
        tmdbId,
        season,
        episode,
        testUrls
      })
    });

    if (!response.ok) {
      console.error(`Embed check API failed: ${response.status} ${response.statusText}`);
      return null;
    }

    const data: EmbedCheckResponse = await response.json();
    return data;
  } catch (error) {
    console.error('Failed to check embed URLs:', error);
    return null;
  }
}

/** Create VideoSource from provider */
export function createVideoSource(
  provider: VideoProvider,
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number,
  additionalData?: Partial<VideoSource>
): VideoSource {
  const url = buildEmbedUrl(provider.value, mediaType, tmdbId, season, episode);
  
  return {
    url,
    quality: "HD",
    type: "iframe",
    servers: `${provider.name} (${provider.hostname})`,
    tmdbId,
    mediaType,
    season,
    episode,
    provider: provider.name,
    ...additionalData
  };
}

/** Log configuration for debugging */
export function logConfiguration(): void {
  const status = getConfigurationStatus();
  console.group('üé¨ Video Configuration Status');
  console.log('Providers:', status.providers);
  console.log('Environment:', status.environment);
  
  if (status.validation.errors.length > 0) {
    console.error('Errors:', status.validation.errors);
  }
  
  if (status.validation.warnings.length > 0) {
    console.warn('Warnings:', status.validation.warnings);
  }
  
  console.groupEnd();
}
