// lib/api/video-tv.ts
import {
  type VideoSource,
  type VideoProvider,
  type EmbedCheckResponse,
  getProvidersForMediaType,
  createVideoSource,
  resolveTmdbId,
  checkEmbedUrls,
  getEmbedUrlCandidates,
  ENV,
  validateConfiguration
} from './video-common';
import axios from 'axios';

/** TV-specific video API */

/** Get all TV-compatible providers */
function getTVProviders(): VideoProvider[] {
  return getProvidersForMediaType('tv');
}

/** Enhanced TV sources with validation */
export async function getTVSources(
  tmdbId: number,
  season?: number,
  episode?: number
): Promise<VideoSource[]> {
  if (!tmdbId || tmdbId <= 0) {
    throw new Error(`Invalid TV show TMDB ID: ${tmdbId}`);
  }

  const seasonNum = season || 1;
  const episodeNum = episode || 1;

  if (seasonNum <= 0 || episodeNum <= 0) {
    throw new Error(`Invalid season/episode: S${seasonNum}E${episodeNum}`);
  }

  const config = validateConfiguration();
  if (!config.isValid) {
    console.error('Video configuration errors:', config.errors);
    throw new Error('Invalid video configuration');
  }

  const providers = getTVProviders();
  if (providers.length === 0) {
    console.warn('No TV-compatible providers configured');
    return [];
  }

  try {
    const sources = providers.map(provider => 
      createVideoSource(provider, "tv", tmdbId, seasonNum, episodeNum, {
        title: `TV Show ${tmdbId}`,
        episodeTitle: `S${seasonNum}E${episodeNum}`
      })
    );

    console.log(`üì∫ Generated ${sources.length} TV sources for TMDB ID: ${tmdbId} S${seasonNum}E${episodeNum}`);
    return sources;
  } catch (error) {
    console.error(`Failed to generate TV sources for TMDB ID ${tmdbId} S${seasonNum}E${episodeNum}:`, error);
    throw error;
  }
}

export async function getTVSourcesWithTesting(
  tmdbId: number,
  season?: number,
  episode?: number,
  testUrls: boolean = true
): Promise<{
  sources: VideoSource[];
  workingSources: VideoSource[];
  embedCheckData: EmbedCheckResponse | null;
}> {
  const seasonNum = season || 1;
  const episodeNum = episode || 1;

  const sources = await getTVSources(tmdbId, seasonNum, episodeNum);

  if (!testUrls) {
    return { sources, workingSources: sources, embedCheckData: null };
  }

  const embedCheckData = await checkEmbedUrls('tv', tmdbId, seasonNum, episodeNum);
  
  if (!embedCheckData) {
    console.warn(`Failed to test TV sources for TMDB ID: ${tmdbId} S${seasonNum}E${episodeNum}`);
    return { sources, workingSources: sources, embedCheckData: null };
  }

  const workingSources = embedCheckData.allUrls
    .filter(url => url.working)
    .map(url => {
      const originalSource = sources.find(s => s.url === url.originalUrl) || sources[0];
      return {
        ...originalSource,
        url: url.url,
        working: url.working,
        responseTime: url.responseTime,
        error: url.error,
        provider: url.provider,
        servers: `${url.provider} (${url.working ? 'Working' : 'Failed'})`
      };
    });

  console.log(`üì∫ TV sources tested: ${workingSources.length}/${sources.length} working for S${seasonNum}E${episodeNum}`);

  return { sources, workingSources, embedCheckData };
}

export async function getTVEpisodeInfo(
  tmdbId: number,
  season: number,
  episode: number
): Promise<{
  episode: any;
  season: any;
  show: any;
} | null> {
  if (!ENV.TMDB_API_KEY) {
    console.warn('TMDB API key not available for episode info');
    return null;
  }

  try {
    const [episodeResponse, seasonResponse, showResponse] = await Promise.all([
      axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}/season/${season}/episode/${episode}`, {
        params: { api_key: ENV.TMDB_API_KEY },
        timeout: 8000
      }).catch(() => ({ data: null })),
      
      axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}/season/${season}`, {
        params: { api_key: ENV.TMDB_API_KEY },
        timeout: 8000
      }).catch(() => ({ data: null })),
      
      axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}`, {
        params: { api_key: ENV.TMDB_API_KEY },
        timeout: 8000
      }).catch(() => ({ data: null }))
    ]);

    return {
      episode: episodeResponse.data,
      season: seasonResponse.data,
      show: showResponse.data
    };
  } catch (error) {
    console.error(`Failed to fetch TV episode info for ${tmdbId} S${season}E${episode}:`, error);
    return null;
  }
}

export async function getTVShowWithEmbed(
  tmdbId: number,
  season?: number,
  episode?: number,
  testUrls: boolean = true
) {
  const seasonNum = season || 1;
  const episodeNum = episode || 1;

  const resolvedTmdbId = await resolveTmdbId(tmdbId, 'tv');
  if (!resolvedTmdbId) {
    throw new Error(`Invalid or unresolvable TV show TMDB ID: ${tmdbId}`);
  }

  try {
    const [tvInfo, sourcesData] = await Promise.all([
      ENV.TMDB_API_KEY ? getTVEpisodeInfo(resolvedTmdbId, seasonNum, episodeNum) : Promise.resolve(null),
      getTVSourcesWithTesting(resolvedTmdbId, seasonNum, episodeNum, testUrls)
    ]);

    const { sources, workingSources, embedCheckData } = sourcesData;

    if (tvInfo?.show?.name || tvInfo?.episode?.name) {
      const showTitle = tvInfo.show?.name;
      const episodeTitle = tvInfo.episode?.name;
      sources.forEach(source => {
        source.title = showTitle;
        source.episodeTitle = episodeTitle || `S${seasonNum}E${episodeNum}`;
      });
      workingSources.forEach(source => {
        source.title = showTitle;
        source.episodeTitle = episodeTitle || `S${seasonNum}E${episodeNum}`;
      });
    }

    const primaryEmbedUrl = workingSources[0]?.url || sources[0]?.url || "";

    const result = {
      tvShow: tvInfo?.show,
      episode: tvInfo?.episode,
      season: tvInfo?.season,
      embedUrl: primaryEmbedUrl,
      sources,
      workingSources,
      embedCheckData,
      metadata: {
        tmdbId: resolvedTmdbId,
        mediaType: 'tv' as const,
        season: seasonNum,
        episode: episodeNum,
        title: tvInfo?.show?.name,
        episodeTitle: tvInfo?.episode?.name,
        airDate: tvInfo?.episode?.air_date,
        runtime: tvInfo?.episode?.runtime,
        sourcesAvailable: sources.length,
        workingSourcesCount: workingSources.length,
        tested: testUrls,
        timestamp: new Date().toISOString()
      }
    };

    console.log(`üì∫ TV data prepared:`, {
      title: tvInfo?.show?.name || `TV Show ${resolvedTmdbId}`,
      episode: `S${seasonNum}E${episodeNum}`,
      episodeTitle: tvInfo?.episode?.name,
      sources: sources.length,
      working: workingSources.length,
      primaryUrl: !!primaryEmbedUrl
    });

    return result;
  } catch (error) {
    console.error(`Error fetching TV show with embed for TMDB ID ${resolvedTmdbId} S${seasonNum}E${episodeNum}:`, error);
    return {
      tvShow: null,
      episode: null,
      season: null,
      embedUrl: "",
      sources: [],
      workingSources: [],
      embedCheckData: null,
      metadata: {
        tmdbId: resolvedTmdbId,
        mediaType: 'tv' as const,
        season: seasonNum,
        episode: episodeNum,
        title: undefined,
        episodeTitle: undefined,
        airDate: undefined,
        runtime: undefined,
        sourcesAvailable: 0,
        workingSourcesCount: 0,
        tested: testUrls,
        timestamp: new Date().toISOString(),
        error: error instanceof Error ? error.message : 'Unknown error'
      }
    };
  }
}

export function getTVEmbedCandidates(
  tmdbId: number,
  season?: number,
  episode?: number
): string[] {
  if (!tmdbId || tmdbId <= 0) {
    throw new Error(`Invalid TV show TMDB ID: ${tmdbId}`);
  }

  const seasonNum = season || 1;
  const episodeNum = episode || 1;
  return getEmbedUrlCandidates('tv', tmdbId, seasonNum, episodeNum);
}

export async function getSeasonEpisodes(
  tmdbId: number,
  season: number
): Promise<any[] | null> {
  if (!ENV.TMDB_API_KEY) {
    console.warn('TMDB API key not available for season episodes');
    return null;
  }

  try {
    const response = await axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}/season/${season}`, {
      params: { api_key: ENV.TMDB_API_KEY },
      timeout: 10000
    });

    return response.data?.episodes || [];
  } catch (error) {
    console.error(`Failed to fetch season ${season} episodes for TV show ${tmdbId}:`, error);
    return null;
  }
}

export async function getAdjacentEpisodes(
  tmdbId: number,
  currentSeason: number,
  currentEpisode: number
): Promise<{
  nextEpisode: { season: number; episode: number } | null;
  previousEpisode: { season: number; episode: number } | null;
}> {
  const episodes = await getSeasonEpisodes(tmdbId, currentSeason);
  
  let nextEpisode = null;
  let previousEpisode = null;

  if (episodes) {
    if (currentEpisode < episodes.length) {
      nextEpisode = { season: currentSeason, episode: currentEpisode + 1 };
    }
    if (currentEpisode > 1) {
      previousEpisode = { season: currentSeason, episode: currentEpisode - 1 };
    }
  }

  return { nextEpisode, previousEpisode };
}

export function hasTVSourcesConfigured(): boolean {
  return getTVProviders().length > 0;
}

export function getTVConfigStatus() {
  const providers = getTVProviders();
  const validation = validateConfiguration();

  return {
    available: providers.length > 0,
    providers: providers.map(p => ({
      name: p.name,
      hostname: p.hostname,
      isTemplate: p.isTemplate,
      priority: p.priority
    })),
    totalProviders: providers.length,
    validation: validation,
    tmdbApiAvailable: !!ENV.TMDB_API_KEY
  };
}

export function debugTVApi(tmdbId?: number, season?: number, episode?: number): void {
  console.group('üì∫ TV Video API Debug');
  const config = getTVConfigStatus();
  console.log('Configuration:', config);

  if (tmdbId) {
    try {
      const candidates = getTVEmbedCandidates(tmdbId, season, episode);
      console.log(`Embed URL candidates for TV ${tmdbId} S${season || 1}E${episode || 1}:`, candidates);
    } catch (error) {
      console.error('Failed to generate candidates:', error);
    }
  }

  console.groupEnd();
}

export { resolveTmdbId as resolveTVTmdbId };

export const tvVideoApi = {
  getTVSources,
  getTVSourcesWithTesting,
  getTVShowWithEmbed,
  getTVEmbedCandidates,
  getTVEpisodeInfo,
  getSeasonEpisodes,
  getAdjacentEpisodes,
  hasTVSourcesConfigured,
  getTVConfigStatus,
  debugTVApi,
  resolveTVTmdbId
};

export default tvVideoApi;
