// lib/api/video-movie.ts
import {
  type VideoSource,
  type VideoProvider,
  type EmbedCheckResponse,
  getProvidersForMediaType,
  createVideoSource,
  resolveTmdbId,
  checkEmbedUrls,
  getEmbedUrlCandidates,
  ENV,
  validateConfiguration
} from './video-common';
import axios from 'axios';

/** Movie-specific video API */

/** Get all movie-compatible providers */
function getMovieProviders(): VideoProvider[] {
  return getProvidersForMediaType('movie');
}

/** Enhanced movie sources with validation */
export async function getMovieSources(tmdbId: number): Promise<VideoSource[]> {
  if (!tmdbId || tmdbId <= 0) {
    throw new Error(`Invalid movie TMDB ID: ${tmdbId}`);
  }

  const config = validateConfiguration();
  if (!config.isValid) {
    console.error('Video configuration errors:', config.errors);
    throw new Error('Invalid video configuration');
  }

  const providers = getMovieProviders();
  if (providers.length === 0) {
    console.warn('No movie-compatible providers configured');
    return [];
  }

  try {
    const sources = providers.map(provider => 
      createVideoSource(provider, "movie", tmdbId, undefined, undefined, {
        title: `Movie ${tmdbId}`
      })
    );

    console.log(`üìΩÔ∏è Generated ${sources.length} movie sources for TMDB ID: ${tmdbId}`);
    return sources;
  } catch (error) {
    console.error(`Failed to generate movie sources for TMDB ID ${tmdbId}:`, error);
    throw error;
  }
}

export async function getMovieSourcesWithTesting(
  tmdbId: number,
  testUrls: boolean = true
): Promise<{
  sources: VideoSource[];
  workingSources: VideoSource[];
  embedCheckData: EmbedCheckResponse | null;
}> {
  const sources = await getMovieSources(tmdbId);

  if (!testUrls) {
    return {
      sources,
      workingSources: sources,
      embedCheckData: null
    };
  }

  const embedCheckData = await checkEmbedUrls('movie', tmdbId);
  
  if (!embedCheckData) {
    console.warn(`Failed to test movie sources for TMDB ID: ${tmdbId}`);
    return {
      sources,
      workingSources: sources,
      embedCheckData: null
    };
  }

  const workingSources = embedCheckData.allUrls
    .filter(url => url.working)
    .map(url => {
      const originalSource = sources.find(s => s.url === url.originalUrl) || sources[0];
      return {
        ...originalSource,
        url: url.url,
        working: url.working,
        responseTime: url.responseTime,
        error: url.error,
        provider: url.provider,
        servers: `${url.provider} (${url.working ? 'Working' : 'Failed'})`
      };
    });

  console.log(`üé¨ Movie sources tested: ${workingSources.length}/${sources.length} working`);

  return {
    sources,
    workingSources,
    embedCheckData
  };
}

export async function getMovieWithEmbed(
  tmdbId: number,
  testUrls: boolean = true
) {
  const resolvedTmdbId = await resolveTmdbId(tmdbId, 'movie');
  if (!resolvedTmdbId) {
    throw new Error(`Invalid or unresolvable movie TMDB ID: ${tmdbId}`);
  }

  try {
    const [movieResponse, sourcesData] = await Promise.all([
      ENV.TMDB_API_KEY
        ? axios.get(`https://api.themoviedb.org/3/movie/${resolvedTmdbId}`, {
            params: { api_key: ENV.TMDB_API_KEY },
            timeout: 10000
          }).catch(error => {
            console.warn(`Failed to fetch movie metadata for ${resolvedTmdbId}:`, error.message);
            return { data: null };
          })
        : Promise.resolve({ data: null }),
      getMovieSourcesWithTesting(resolvedTmdbId, testUrls)
    ]);

    const movie = movieResponse?.data;
    const { sources, workingSources, embedCheckData } = sourcesData;

    if (movie?.title) {
      sources.forEach(source => { source.title = movie.title; });
      workingSources.forEach(source => { source.title = movie.title; });
    }

    const primaryEmbedUrl = workingSources[0]?.url || sources[0]?.url || "";

    const result = {
      movie,
      embedUrl: primaryEmbedUrl,
      sources,
      workingSources,
      embedCheckData,
      metadata: {
        tmdbId: resolvedTmdbId,
        mediaType: 'movie' as const,
        title: movie?.title,
        releaseDate: movie?.release_date,
        runtime: movie?.runtime,
        sourcesAvailable: sources.length,
        workingSourcesCount: workingSources.length,
        tested: testUrls,
        timestamp: new Date().toISOString()
      }
    };

    console.log(`üé¨ Movie data prepared:`, {
      title: movie?.title || `Movie ${resolvedTmdbId}`,
      sources: sources.length,
      working: workingSources.length,
      primaryUrl: !!primaryEmbedUrl
    });

    return result;
  } catch (error) {
    console.error(`Error fetching movie with embed for TMDB ID ${resolvedTmdbId}:`, error);
    return {
      movie: null,
      embedUrl: "",
      sources: [],
      workingSources: [],
      embedCheckData: null,
      metadata: {
        tmdbId: resolvedTmdbId,
        mediaType: 'movie' as const,
        title: undefined,
        releaseDate: undefined,
        runtime: undefined,
        sourcesAvailable: 0,
        workingSourcesCount: 0,
        tested: testUrls,
        timestamp: new Date().toISOString(),
        error: error instanceof Error ? error.message : 'Unknown error'
      }
    };
  }
}

export function getMovieEmbedCandidates(tmdbId: number): string[] {
  if (!tmdbId || tmdbId <= 0) {
    throw new Error(`Invalid movie TMDB ID: ${tmdbId}`);
  }

  return getEmbedUrlCandidates('movie', tmdbId);
}

export function hasMovieSourcesConfigured(): boolean {
  return getMovieProviders().length > 0;
}

export function getMovieConfigStatus() {
  const providers = getMovieProviders();
  const validation = validateConfiguration();

  return {
    available: providers.length > 0,
    providers: providers.map(p => ({
      name: p.name,
      hostname: p.hostname,
      isTemplate: p.isTemplate,
      priority: p.priority
    })),
    totalProviders: providers.length,
    validation: validation,
    tmdbApiAvailable: !!ENV.TMDB_API_KEY
  };
}

export function debugMovieApi(tmdbId?: number): void {
  console.group('üé¨ Movie Video API Debug');
  const config = getMovieConfigStatus();
  console.log('Configuration:', config);

  if (tmdbId) {
    try {
      const candidates = getMovieEmbedCandidates(tmdbId);
      console.log(`Embed URL candidates for movie ${tmdbId}:`, candidates);
    } catch (error) {
      console.error('Failed to generate candidates:', error);
    }
  }

  console.groupEnd();
}

export { resolveTmdbId as resolveMovieTmdbId };

export const movieVideoApi = {
  getMovieSources,
  getMovieSourcesWithTesting,
  getMovieWithEmbed,
  getMovieEmbedCandidates,
  hasMovieSourcesConfigured,
  getMovieConfigStatus,
  debugMovieApi,
  resolveMovieTmdbId
};

export default movieVideoApi;
